import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { EditSubCategoryComponent } from '../edit-sub-category/edit-sub-category.component';
import { RouterModule, Routes } from '@angular/router';
import { LayoutModule } from '../../../../layouts/layout.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DefaultComponent } from '../../../default/default.component';
import { NgxPaginationModule } from 'ngx-pagination';
const routes: Routes = [
    {
        path: "",
        component: DefaultComponent,
        children: [
            {
                path: "",
                component: EditSubCategoryComponent
            }
        ]
    },
];

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes),
        LayoutModule, FormsModule, ReactiveFormsModule, NgxPaginationModule,
    ],
    declarations: [EditSubCategoryComponent]
})
export class EditSubCategoryModule { }
