import { Component, OnInit } from '@angular/core';
import { SubCategoryService } from '../../../../../auth/_services/subCategory.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from "@angular/router";
import { MenuService } from '../../../../../auth/_services/menu.service';
import { ToastrService } from 'ngx-toastr';
@Component({
    selector: 'app-edit-sub-category',
    templateUrl: "edit-sub-category.component.html",
    styles: []
})
export class EditSubCategoryComponent implements OnInit {

    form: FormGroup;
    private formSumitAttempt: boolean;

    constructor(private toastr: ToastrService, private route: ActivatedRoute, private _menuService: MenuService, private router: Router, private _subCategoryService: SubCategoryService, private formBuilder: FormBuilder) { }

    public subCategoryData = "";
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    public menuDrpdown: any;
    public categoryDrpdown = '';
    public selectedMenu = '';
    public selectedStatus = '';
    public selectedCategory = '';
    p: number = 1;

    sub_cat_id = '';
    id = '';
    name = '';

    ngOnInit() {
        /* localStorage.setItem('section', 'subCategory');
         this._subCategoryService.getAll().subscribe(
             data => {
                 this.subCategoryData = data.success;
                 this.menuDrpdown = data.menu;
                 console.log(this.subCategoryData);
             }
         );
 */

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.product_subcategory != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/



        this.route.params.subscribe(
            params => {
                console.log('sub_cat_id', params.sub_cat_id);

                if (params.sub_cat_id) {
                    this.sub_cat_id = params.sub_cat_id;
                    this._subCategoryService.getById(params.sub_cat_id).subscribe(
                        data => {
                            //console.log('category data', data.success);
                            this.id = data.success.id;
                            this.selectedMenu = data.success.menu_id;
                            this.selectedCategory = data.success.category_id;
                            this.name = data.success.name;
                            this.selectedStatus = data.success.status;

                            this.get_category();
                        }




                    );
                }
            }
        );



        this.form = this.formBuilder.group({
            id: [null],
            menu_id: [null, Validators.required],
            category_id: [null, Validators.required],
            name: [null, Validators.required],
            status: [null, Validators.required]
        });


        this._menuService.searching({ dropdown_value: 'Active', search_value: '' }).subscribe(
            data => {
                this.menuDrpdown = data.success;
            }
        );

    }


    isFieldValid(field: string) {
        return (
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }

    onSubmit() {
        this.formSumitAttempt = true;
        if (this.form.valid) {
            let that = this;
            this._subCategoryService.updateSubCategory({ form_data: this.form.value }).subscribe(
                data => {
                    this.subCategoryData = data.success;
                    this.menuDrpdown = data.menu;
                    this.toastr.success('Sub-category update Successfully', 'Success Message');
                    this.reset();
                    this.router.navigate(['/category/sub-category-list']);
                }
            );
        }
    }

    reset() {
        if (this.selectedMenu != '' && this.selectedCategory != '' && this.selectedStatus != '') {
            this.form.reset();
            this.selectedMenu = '';
            this.selectedStatus = '';
            this.selectedCategory = '';
            this.formSumitAttempt = false;
        }
    }



    public subCategory_delete(del_id) {
        console.log(del_id);
        let value = confirm('Are u want to delete this sub-category?');
        if (value) {
            this._subCategoryService.subCategoryDelete(del_id).subscribe(
                data => {
                    this.subCategoryData = data.success;
                    this.menuDrpdown = data.data;
                }
            );
        }
    }


    public changeSubCategoryStatus(subCategory: any) {

        console.log(subCategory);

        if (subCategory.status == 'Active') {
            subCategory.status = 'Inactive';
            this._subCategoryService.updateStatus(subCategory.id).subscribe(

                data => {
                    console.log('update', data);
                }
            );
        }
        else {
            subCategory.status = 'Active';
            this._subCategoryService.updateStatus(subCategory.id).subscribe(

                data => {
                    console.log('update', data);
                }
            );
        }
    }

    public search() {
        this._subCategoryService.searching({ search_value: this.searchValue, dropdown_value: this.dropDownValue }).subscribe(
            data => {
                this.subCategoryData = data.success;
            }
        );
    }



    public get_category() {
        console.log(this.selectedMenu);
        this._subCategoryService.getCategory({ menu_id: this.selectedMenu }).subscribe(
            data => {
                this.categoryDrpdown = data.success;
                console.log(this.categoryDrpdown);
            }
        )
    }






}